import React, { useContext } from "react";
import { BrowserRouter as Router, Switch, Route, Link } from "react-router-dom";
import UserContext from "./usercontext";

function Users() {
  let user = useContext(UserContext);
  return (
    <>
      <div class="d-sm-flex align-items-center justify-content-between mb-4">
        <h1 class="h3 mb-0 text-gray-800">Users</h1>
        <div>
          <Link
            to="/createuser"
            class="d-none d-sm-inline-block btn btn-sm btn-primary shadow-sm mr-4"
          >
            <i class="fas fa-plus fa-sm text-white-50"></i>Create Users
          </Link>
        </div>
      </div>

      <div class="row">
        {" "}
        <div class="card shadow mb-4">
          <div class="card-header py-3">
            <h6 class="m-0 font-weight-bold text-primary">
              DataTables Example
            </h6>
          </div>
          <div class="card-body">
            <div class="table-responsive">
              <table
                class="table table-bordered"
                id="dataTable"
                width="100%"
                cellspacing="0"
              >
                <thead>
                  <tr>
                    <th>Name</th>
                    <th>Position</th>
                    <th>Office</th>
                    <th>Age</th>
                    <th>Start date</th>
                    <th>Action</th>
                  </tr>
                </thead>
                <tfoot>
                  <tr>
                    <th>Name</th>
                    <th>Position</th>
                    <th>Office</th>
                    <th>Age</th>
                    <th>Start date</th>
                    <th>Action</th>
                  </tr>
                </tfoot>
                <tbody>
                  <tr>
                    <td> dummy1</td>
                    <td>dummy1</td>
                    <td>Edinburgh</td>
                    <td>61</td>
                    <td>2011/04/25</td>
                    <td>
                      <Link
                        to="/edituser/:id"
                        class="d-none d-sm-inline-block btn btn-sm btn-primary shadow-sm"
                      >
                        <i class="fas fa-edit fa-sm text-white-50"></i> Edit
                        Users
                      </Link>
                    </td>
                  </tr>
                  {user.userList.map((item) => {
                    return (
                      <tr>
                        <td>{item.userName}</td>
                        <td>{item.userEmail}</td>
                        <td>Edinburgh</td>
                        <td>61</td>
                        <td>2011/04/25</td>
                        <td>
                          <Link
                            to={`/edituser/${item.userName}`}
                            class="d-none d-sm-inline-block btn btn-sm btn-primary shadow-sm"
                          >
                            <i class="fas fa-edit fa-sm text-white-50"></i> Edit
                            Users
                          </Link>
                        </td>
                      </tr>
                    );
                  })}
                </tbody>
              </table>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default Users;
