import React, { useContext, useState } from "react";
import { BrowserRouter as Router, Switch, Route, Link } from "react-router-dom";
import UserContext from "./usercontext";

function EditUser(props) {
  let user = useContext(UserContext);
  let [userName, setUserName] = useState(props.match.params.user);

  let emailid = "";
  {
    user.userList.map((item) => {
      console.log(item);
      if (userName == item.userName) {
        emailid = item.userEmail;
      }
    });
  }

  let [userEmail, setUserEmail] = useState(emailid);

  let userData = { userName, userEmail };
  // console.log(user.userList)

  // console.log(props);
  return (
    <>
      <div class="d-sm-flex align-items-center justify-content-between mb-4">
        <h1 class="h3 mb-0 text-gray-800">
          Edit Users {props.match.params.user}
        </h1>
        <div>
          <Link
            to="/createuser"
            class="d-none d-sm-inline-block btn btn-sm btn-primary shadow-sm mr-4"
          >
            <i class="fas fa-plus fa-sm text-white-50"></i>Create Users
          </Link>
          <Link
            to="/users/"
            class="d-none d-sm-inline-block btn btn-sm btn-primary shadow-sm"
          >
            <i class="fas fa-users fa-sm text-white-50"></i>
            Users
          </Link>
        </div>
      </div>

      {/* <div class="row">Edit User page {props.match.params.id}</div> */}
      <div class="row">
        <div class="offset-3 col-6">
          <form
            onSubmit={(e) => {
              e.preventDefault();
              user.setUserList([userData]);
              setUserName("");
              setUserEmail("");
              // console.log(user);
            }}
          >
            <label>Name</label>
            <input
              type="text"
              class="form-control"
              value={userName}
              onChange={(e) => {
                setUserName(e.target.value);
              }}
            ></input>
            <label>Email</label>
            <input
              type="email"
              class="form-control"
              value={userEmail}
              onChange={(e) => {
                setUserEmail(e.target.value);
              }}
            ></input>
            <Link to="/users">
              <button class="mt-2 d-none d-sm-inline-block btn btn-sm btn-primary shadow-sm mr-4">
                <i class="fas fa-plus fa-sm text-white-50"></i>Save changes
              </button>
            </Link>
          </form>
        </div>
      </div>
    </>
  );
}
export default EditUser;
