import React from "react";

function Valuecard(props) {
  return (
    <>
      <div
        onClick={() => props.click(props.details)}
        class="col-xl-3 col-md-6 mb-4 valueCard"
      >
        <div
          class={`card border-left-${props.details.color} shadow h-100 py-2`}
        >
          <div class="card-body">
            <div class="row no-gutters align-items-center">
              <div class="col mr-2">
                <div
                  class={`text-xs font-weight-bold text-${props.details.color} text-uppercase mb-1`}
                >
                  {props.details.title}
                </div>
                <div class="h5 mb-0 font-weight-bold text-gray-800">
                  {props.details.value}
                </div>
              </div>
              <div class="col-auto">
                <i class={`fas ${props.details.icon} fa-2x text-gray-300`}></i>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}
export default Valuecard;
