import React, { useContext, useState } from "react";
import { BrowserRouter as Router, Switch, Route, Link } from "react-router-dom";
import UserContext from "./usercontext";

function CreateUser() {
  let user = useContext(UserContext);
  let [userName, setUserName] = useState("");
  let [userEmail, setUserEmail] = useState("");
  let userData = { userName, userEmail };

  return (
    <>
      <div class="d-sm-flex align-items-center justify-content-between mb-4">
        <h1 class="h3 mb-0 text-gray-800">Create New User</h1>
        <div>
          <Link
            to="/edituser/:id"
            class="d-none d-sm-inline-block btn btn-sm btn-primary shadow-sm mr-4"
          >
            <i class="fas fa-edit fa-sm text-white-50"></i> Edit Users
          </Link>
          <Link
            to="/users"
            class="d-none d-sm-inline-block btn btn-sm btn-primary shadow-sm"
          >
            <i class="fas fa-users fa-sm text-white-50"></i>Users
          </Link>
        </div>
      </div>

      <div class="row">
        <div class="offset-3 col-6">
          <form
            onSubmit={(e) => {
              e.preventDefault();
              user.setUserList([...user.userList, userData]);
              setUserName("");
              setUserEmail("");
            }}
          >
            <label>Name</label>
            <input
              type="text"
              class="form-control"
              value={userName}
              onChange={(e) => {
                setUserName(e.target.value);
              }}
            ></input>
            <label>Email</label>
            <input
              type="email"
              class="form-control"
              value={userEmail}
              onChange={(e) => {
                setUserEmail(e.target.value);
              }}
            ></input>
            <button class="mt-2 d-none d-sm-inline-block btn btn-sm btn-primary shadow-sm mr-4">
              <i class="fas fa-plus fa-sm text-white-50"></i>Add Users
            </button>
          </form>
        </div>
      </div>
    </>
  );
}
export default CreateUser;
